<template id="home">
  <div class="bed-all">
    <img src="../assets/img/bg.png" alt="" class='bg'>
    <Topbar>
      <i class='iconfont'>&#xe62d;</i>
      <span>{{$t("bed.title")}}</span>
    </Topbar>
    <div class="bed-main">
      <div class="bed-choose">
        <div class="bed-row">
          <div class="row-left">
            <span class="choose-title">{{$t("bed.ksxz")}}:</span>
            <el-select class='select' v-model='currentDepartmentId' filterable :placeholder="$t('message.select')" @change='selectDepartment'>
             <el-option
               v-for="item in departmentList"
               :key="item.departmentId"
               :label="item.name"
               :value="item.departmentId">
             </el-option>
           </el-select>
          </div>
          <div class="row-right"></div>
        </div>
      </div>
      <div class="main clearfix">
        <div class="main-item" v-for="(item,index) in roomsList" :key='index'>
          <span class='item-title'>{{item.name}}</span>
          <div class="items">
            <div class="item" v-for="(room,index) in item.rooms" :key='index'>
              <div class="item-top">{{room.name}}</div>
              <div class="item-bottom">
                <span v-for="(bed,index) in room.beds" :key='index'>{{bed.name}}</span>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script type="text/javascript">
import Vue from "vue";
var that= {};
import Topbar from '../components/topbar'
import { getRoomsList } from '@/api/bed'
export default {
  name:'bed',
  data(){
   return{
     departmentList:that.$store.state.monitor.departmentList,
     currentDepartmentId:'',
     roomsList:[],
   }
  },
  computed:{

  },
  beforeCreate(){
    that = this;
  },
  created(){

  },
  mounted:function(){
    console.log(that.$store.state.monitor.departmentList)
    that.currentDepartmentId = that.$store.state.monitor.departmentList[0].departmentId;
    that.getRoomsListFn();
  },
  methods:{
    selectDepartment(id){
      console.log(id)
      that.currentDepartmentId = id;
      that.getRoomsListFn();
    },
    getRoomsListFn(){
      var data={
        departmentId:that.currentDepartmentId
      }
      getRoomsList(data).then((res)=>{
        if(res.code==200 && res.success){
          that.roomsList = res.data;
        }
      })
    },
  },
  components:{
    Topbar
  }
}
</script>
<style lang="scss">
html,body{
width:100%;
height:100%;
  .bed-all{
    width:100%;
    background-size:cover;
    height:100%;
    position:relative;
    padding-top:40px;
    .bed-main{
      position:relative;
      margin:0 auto;
      width:96%;
      .bed-choose{
        width:86%;
        margin:0 auto;
        padding-top: 47px;
        display:flex;
        flex-direction: column;
        .bed-row{
          display:flex;
          flex-direction: row;
          justify-content: space-between;
          margin-bottom:23px;
          .row-left{
            width:50%;
            display:flex;
            flex-direction: row;
            .choose-title{
              font-size:18px;
              font-family:PingFangSC-Medium,PingFang SC;
              font-weight:600;
              color:rgba(0,0,0,1);
              line-height:36px;
              margin-right:7px;
              height:36px;
            }
            .choose-title1{
              margin:0 17px;
            }
          }
          .row-right{
            width:50%;
            display:flex;
            flex-direction: row;
            justify-content: flex-end;
          }
        }
      }
      .main{
        display:flex;
        flex-direction: row;
        flex-wrap: wrap;
        width:100%;
        .main-item{
          width:100%;
          .item-title{
            margin-left:125px;
            font-family: PingFangSC-Medium;
            font-size: 24px;
            color: #000000;
            text-align: left;
            margin-bottom: 10px;
            display:block;
            float:left;
          }
          .items{
            display:flex;
            flex-direction: row;
            flex-wrap: wrap;
            width:100%;
            .item{
              width: 12.5%;
              height: 100px;
              float: left;
              border: 1px solid #ddd;
              background: #fff;
              box-sizing: border-box;
              .item-top{
                text-align:center;
                font-family: PingFangSC-Medium;
                font-size: 24px;
                height:33px;
                margin-top:18px;
                color: #1890FF;
                text-align: center;
              }
              .item-bottom{
                display:flex;
                justify-content: space-around;
                span{
                  display:block;
                  font-family: PingFangSC-Medium;
                  font-size: 14px;
                  color: #000000;
                  margin-top:10px;
                  text-align: center;
                }
              }
            }
          }
        }
      }
    }
  }
}
</style>



